<template>
	<div class="register">
		<!--  忘记密码-->
		<div class="denglu">
			<div class="denglu-dl">注册</div>
			<!--输入手机号，验证码，密码-->
			<div class="denglu-inp">
				<div class="inp inp1">
					<img src="/img/login-shoujihao.png" alt="">
					<input type="text" name="user" placeholder="手机号" v-model="username">
				</div>
				<div class="inp inp2">
					<img src="/img/login-yzm.png" alt="">
					<input type="text" name="yzm" value="1234" placeholder="验证码" v-model="yanzhengma">
					<button class="btn-default btn">获取验证码</button>
				</div>
				<div class="inp inp3">
					<img src="/img/login-mima.png" alt="">
					<input type="password" name="password" placeholder="设置密码" v-model="password">
				</div>
			</div>
			<!--是否同意用户注册协议-->
			<div class="accept">
				<input type="checkbox" @click="mychecked">
				<span>我已同意并阅读<a href="#">《食客生鲜用户注册协议》</a></span>
			</div>
			<!--登录按钮-->
			<div class="denglu-btn">
				<button class="btn-default btn" @click="submit">注册</button>
			</div>
			<!--未收到验证码-->
			<div class="yzm">
				<div class="yzm-yzm" @click="tanChu">未收到验证码？</div>
			</div>
		</div>
		<!--  快速登录-->
		<div class="bj">
			<div class="top">快速登录</div>
			<div class="bottom">
				<ul>
					<li v-for="(item, index) in loginLogo" :key="index">
						<img :src="item" alt="">
					</li>
				</ul>
			</div>
		</div>
		<!-- 弹出层-->
		<div class="tanchuceng" v-show="show"></div>
		<div class="tanchu" v-show="show">
			<div class="tanchu-top">
				<p>我们将电话语音告知您验证码</p>
				<p>如仍未接到电话，请拨打客服电话</p>
				<p>400-730-0770</p>
			</div>
			<div class="tanchu-bottom">
				<div @click="tanChuFalse">取消</div>
				<div>现在接听</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'register',
		data: function() {
			return {
				loginLogo: [
					'/img/weixin.png'
				],
				show: false,
				username: null,
				yanzhengma: null,
				password: '',
				len: 11,
				checked: false
			}
		},
		methods: {
			// 弹出层弹出
			tanChu() {
				this.show = true
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = 'hidden'
				document.addEventListener('touchmove', mo, false)
			},
			// 弹出层隐藏
			tanChuFalse() {
				this.show = false
				var mo = function(e) {
					e.preventDefault()
				}
				document.body.style.overflow = ''
				document.addEventListener('touchmove', mo, false)
			},
			mychecked () {
				this.checked = !this.checked
				console.log(this.checked)
			},
			// 提交
			submit() {
				var regx = /[a-zA-Z0-9]{8,16}/
				// 判断手机号，验证码，密码输入格式是否正确
        if(this.username){
         // eslint-disable-next-line eqeqeq
				if(this.username.length == this.len) {
					// eslint-disable-next-line eqeqeq
					if(this.yanzhengma == 1234) {
						if(regx.test(this.password)) {
							if(this.checked) {
								// 发送ajax请求数据
								this.$axios.get('/json/users.json').then(res => {
									console.log(res.data)
									for(var i in res.data) {
										// eslint-disable-next-line eqeqeq
										if(this.username == res.data[i].username) {
											alert('用户名已经被占用')
											break
										} else {
											// eslint-disable-next-line eqeqeq
											if(i == res.data.length - 1) {
												console.log('添加用户信息到数据库')
												alert('注册成功')
												//注册成功跳转到登陆页面
												this.$router.push('/login')
											} else {
												continue
											}
										}
									}
								}).catch(err => {
									console.log(err)
								})
							} else {
								console.log('请阅读并同意协议')
							}
						} else {
							console.log('密码格式错误，必须包涵数字、字母，并且在8-16位')
						}
					} else {
						console.log('验证码错误')
					}
				} else {
					console.log('用户名格式错误')
				}
        }else{
          alert("输入账号为空！")
        }
			}
		}
	}
</script>

<style scoped>
	a {
		text-decoration: none;
		color: #84c225;
	}

	.register {
