		/*position:absolute;*/
		/*left:0;*/
		/*bottom: 0;*/
		margin-top: 200px;
		background: url("/img/login-bj.png") no-repeat;
	}

	.register .top {
		width: 100%;
		height: 100px;
		font-size: 28px;
		line-height: 100px;
		/*font-weight: bold;*/
		text-align: center;
		color: white;
	}

	.register .bottom ul {
		width: 100%;
		display: flex;
		justify-content: center;
		margin: 10px 0 0 0;
	}

	.register .bottom ul li {
		width: 100px;
		height: 100px;
		border-radius: 50%;
		background-color: #3fb135;
		text-align: center;
		line-height: 100px;
		margin: 0 20px;
	}
	/*登录*/

	.register .denglu {
		width: 690px;
		margin: 0 auto;
	}

	.register .denglu-dl {
		width: 100%;
		height: 100px;
		font-size: 30px;
		line-height: 100px;
		text-align: center;
		margin: 60px 0 0 0;
	}
	/*  输入手机号，密码*/

	.register .denglu-inp {
		width: 580px;
		margin: 100px auto 0;
	}

	.register .denglu-inp .inp1 {
		margin-bottom: 60px;
	}

	.register .denglu-inp .inp {
		margin-left: 10%;
		margin-right: 50px;
		border-bottom: 1px solid #c0c0c0;
		line-height: 60px;
	}

	.register .denglu-inp .inp input {
		width: 80%;
