		color: white;
	}

	.register .denglu-inp .inp3 {
		position: relative;
	}

	.register .denglu-inp .inp3 .img {
		position: absolute;
		top: 0;
		right: 25px;
		height: 60px;
		font-size: 20px;
		line-height: 60px;
		color: white;
	}
	/*  是否同意用户注册协议*/

	.register .accept {
		width: 80%;
		margin: 60px auto 0;
		font-size: 16px;
		line-height: 30px;
		/*text-align: center;*/
		padding-left: 40px;
	}
	/*登录按钮*/

	.register .denglu-btn {
		width: 500px;
		margin: 0 0 0 95px;
	}

	.register .denglu-btn .btn {
		width: 100%;
		height: 50px;
		background-color: #84c225;
		color: white;
		font-size: 20px;
		border-radius: 25px;
	}
	/*未收到验证码*/

	.register .yzm {
		width: 490px;
