		left: 0;
		top: 0;
		opacity: 0.4;
	}

	.tanchu {
		width: 350px;
		height: 200px;
		padding: 20px 20px 20px 30px;
		background-color: white;
		position: fixed;
		left: 200px;
		top: 175px;
	}

	.tanchu .tanchu-top p:first-child {
		font-size: 22px;
		font-weight: bold;
	}

	.tanchu .tanchu-top p:nth-child(2) {
